@charset "UTF-8";
/*Обнуление*/
@import url("https://fonts.googleapis.com/css?family=Roboto:400,700,900&subset=cyrillic");
* {
  padding: 0;
  margin: 0;
  border: 0; }

*, *:before, *:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box; }

:focus, :active {
  outline: none; }

a:focus, a:active {
  outline: none; }

nav, footer, header, aside {
  display: block; }

html, body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%; }

input, button, textarea {
  font-family: inherit; }

input::-ms-clear {
  display: none; }

button {
  cursor: pointer; }

button::-moz-focus-inner {
  padding: 0;
  border: 0; }

a, a:visited {
  text-decoration: none; }

a:hover {
  text-decoration: none; }

ul li {
  list-style: none; }

img {
  vertical-align: top; }

h1, h2, h3, h4, h5, h6 {
  font-size: inherit;
  font-weight: inherit; }

/*--------------------*/
a {
  color: #000 !important;
  text-decoration: none; }

a.text:active,
a.text:hover,
a.text {
  text-decoration: none;
  color: #666; }

* {
  margin: 0;
  padding: 0; }

html {
  font-size: 16px;
  color: #000; }

body {
  font-family: 'Roboto', 'Arial', sans-serif;
  overflow-wrap: break-word;
  word-wrap: break-word; }

.container {
  max-width: 100%;
  min-width: 320px; }

.content {
  display: flex;
  flex-direction: column;
  flex: 1 0 auto; }

.wrap {
  min-width: 320px;
  max-width: 1024px;
  padding: 0 15px;
  margin: 0 auto;
  box-sizing: border-box; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0, 0, 0, 0);
  overflow: hidden; }

.header {
  color: #000;
  height: 93.5vh;
  overflow: hidden;
  text-align: center; }

.naw {
  font-size: 30px; }

.header__about {
  align-content: center;
  text-align: center;
  font-weight: bold;
  font-size: 1.5rem; }

.header__img {
  position: relative;
  width: 100%;
  /* for IE 6 */
  opacity: .3; }
  .header__img img {
    width: 100%;
    height: 100%;
    object-fit: cover; }

.header__content {
  position: absolute;
  top: 42%;
  left: 17%;
  color: #000; }

.header__list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-content: center;
  text-align: center;
  font-size: 20px; }

img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.footer {
  flex-shrink: 0;
  background: grey;
  color: #000; }

.footer__menu {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  height: 50px; }
  .footer__menu li {
    margin: 0 10px 0 10px; }

